import React, { FC, ReactElement, useEffect, useState } from "react";
import { FaFemale, FaMale } from "react-icons/fa";
import { MdAccessible, MdGroup, MdOutlineHouse } from "react-icons/md";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import InputSelect from "../../components/Inputs/InputSelect";
import Chart from "react-apexcharts";
import SelectedDistrictTransferRequest from "./SelectedDistrictTransferRequest";
import {
  FC_GetTransferDashboardCountry,
  Total_transfer_request_Numbers__Interface,
  TransferDashboard_Request,
  VacantPosts__Dashboard_Interface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Loading from "../../components/Loading/Loading";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";

type TransferRequestDashboardProps = {};

const TransferRequestDashboard: FC<
  TransferRequestDashboardProps
> = (): ReactElement => {
  const [transfer_total, setTransfer_total] =
    useState<Total_transfer_request_Numbers__Interface>({
      total_transfer_request: 0,
      female: 0,
      male: 0,
      single: 0,
      married: 0,
      disability: 0,
    });
  const [transfers_requests, setTransfers_requests] = useState<
    TransferDashboard_Request[]
  >([]);
  const [vacant_posts, setVacant_posts] = useState<
    VacantPosts__Dashboard_Interface[]
  >([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [selected_position, setSelectedPosition] = useState<string>("0");
  const [main_loading, setMainLoading] = useState<boolean>(true);
  const [error, setError] = useState<{
    target: "main-success" | "main-error" | null;
    msg: string;
  }>({
    msg: "",
    target: null,
  });

  useEffect(() => {
    if (main_loading === true) {
      FC_GetTransferDashboardCountry("1", "0", (status, data, msg) => {
        if (status === false)
          setError({
            target: "main-error",
            msg: msg,
          });

        if (data !== null) {
          setTransfer_total(data.transfer_total);

          // let data_new = data.vacant_posts.map(el => el.)
          // let data_new = data.transfers_requests.map(el => el.)
          setTransfers_requests(data.transfers_requests);
          setVacant_posts(data.vacant_posts);
        }

        setMainLoading(false);
      });
    }
  }, [main_loading]);
  const [tabGraph, settabGraph] = useState<boolean>(true);
  const [selectedDistrict, setSelectedDistrict] =
    useState<TransferDashboard_Request | null>(null);

  const loadDataByPositionCode = (position: string) => {
    setSelectedPosition(position);
    setLoading(true);
    FC_GetTransferDashboardCountry("1", position, (status, data, msg) => {
      if (status === false)
        setError({
          target: "main-error",
          msg: msg,
        });

      if (data !== null) {
        setTransfers_requests(data.transfers_requests);
        setTransfer_total(data.transfer_total);
        if (position === "0") setVacant_posts(data.vacant_posts);
      }

      setMainLoading(false);
      setLoading(false);
    });
  };

  const dataPointSelection = (e: any, chart: any, opts: any) => {
    let selectedData = transfers_requests[opts.dataPointIndex];
    if (selectedData) setSelectedDistrict(selectedData);
  };

  let chartDataVacantVsRequests = {
    options: {
      chart: {
        id: "basic-bar-vacant-vs-bar",
        stacked: false,
        toolbar: {
          show: true,
        },
        zoom: {
          enabled: true,
        },
        events: {
          dataPointSelection: dataPointSelection,
        },
      },

      xaxis: {
        categories: transfers_requests.map((el) => el.district_name),
      },
    },
    series: [
      {
        name: "Vacant posts",
        data: transfers_requests.map((el) => el.district_vacant),
      },
      {
        name: "Requests",
        data: transfers_requests.map((el) => el.total_transfer_request),
      },
    ],
  };

  let chartData = {
    options: {
      chart: {
        id: "basic-bar",
        stacked: true,
        toolbar: {
          show: true,
        },
        zoom: {
          enabled: true,
        },

        events: {
          dataPointSelection: dataPointSelection,
        },
      },
      colors: ["#b73377", "#2ea9d9"],
      xaxis: {
        categories: transfers_requests.map((el) => el.district_name),
      },
    },
    series: [
      {
        name: "Female",
        data: transfers_requests.map((el) => el.total_female),
      },
      {
        name: "Male",
        data: transfers_requests.map((el) => el.total_male),
      },
    ],
  };

  if (selectedDistrict !== null)
    return (
      <SelectedDistrictTransferRequest
        district={selectedDistrict}
        close={setSelectedDistrict.bind(this, null)}
      />
    );

  /* function body */
  return (
    <>
      <div className="lg:sticky top-10 z-50">
        {error.target === "main-error" && (
          <Alert theme="danger">{error.msg}</Alert>
        )}
        {error.target === "main-success" && (
          <Alert theme="success">{error.msg}</Alert>
        )}
        <DashboardContainer
          noPadding
          className="flex p-0.5 mx-2 px-3 flex-col items-center lg:flex-row"
        >
          <div className="flex-1">
            <h1 className="text-2xl font-bold">Transfer Requests</h1>
            <h2 className="text-lg text-accent-900">2020 - 2022</h2>
          </div>
          <div className="p-1  flex-1 gap-x-2">
            <InputSelect
              onChange={(e) => loadDataByPositionCode(e.target.value)}
              size="xs"
              Label_className="text-sm opacity-80"
              value={selected_position}
              error={""}
              label={"Position"}
              placeholder={""}
              name={""}
              disabled={loading}
              data={[
                { title: "All positions", value: "0" },
                ...vacant_posts.map((itm) => ({
                  title: `${itm.position_code} - ${itm.position_name}`,
                  value: itm.position_code,
                })),
              ]}
            />
          </div>
        </DashboardContainer>
      </div>

      {/* <Button onClick={loadData}>Load data</Button> */}
      {loading === true || main_loading === true ? (
        <>
          <div className="py-10 text-center">
            <Loading />
          </div>
        </>
      ) : (
        <>
          <DashboardContainer
            className="px-2 grid-col-2 grid md:grid-cols-3 lg:grid-cols-5 gap-2 pt-3"
            noPadding
            transparent
          >
            {[
              {
                icon: MdGroup,
                title: "Transfers request",
                value: transfer_total.total_transfer_request,
              },
              { icon: FaFemale, title: "Female", value: transfer_total.female },
              { icon: FaMale, title: "Total male", value: transfer_total.male },
              {
                icon: MdOutlineHouse,
                title: "Married",
                value: transfer_total.married,
                value_2: transfer_total.total_transfer_request,
              },
              {
                icon: MdAccessible,
                title: "With disability",
                value: transfer_total.disability,
              },
            ].map((itm, i) => {
              let Icon = itm.icon;
              return (
                <div
                  key={i}
                  className="flex items-center p-1 rounded border bg-white"
                >
                  <section className="m-3 text-4xl bg-yellow-600 bg-opacity-10 rounded-full p-2 text-yellow-800">
                    <Icon />
                  </section>
                  <section>
                    <h5 className="text-gray-500 m-0">{itm.title}</h5>
                    <h2 className="text-yellow-800 font-bold text-3xl -mt-1">
                      {itm.value}{" "}
                      {itm?.value_2 && (
                        <span className="font-light text-gray-400">
                          of {itm?.value_2}
                        </span>
                      )}
                    </h2>
                  </section>
                </div>
              );
            })}
          </DashboardContainer>

          <DashboardContainer className="">
            <section className="p-1 bg-yellow-700 bg-opacity-10 rounded-lg text-sm w-80 mx-auto flex">
              <button
                className={`px-2 flex-1 ${
                  tabGraph === true ? "bg-yellow-600 text-white" : "text-black"
                } py-1 rounded-lg`}
                onClick={settabGraph.bind(this, true)}
              >
                Vacant vs Requests
              </button>
              <button
                className={`px-2 flex-1 ${
                  tabGraph === false ? "bg-yellow-600 text-white" : "text-black"
                } py-1 rounded-lg`}
                onClick={settabGraph.bind(this, false)}
              >
                District {"&"} Gender
              </button>
            </section>
            <Chart
              options={
                tabGraph ? chartDataVacantVsRequests.options : chartData.options
              }
              series={
                tabGraph ? chartDataVacantVsRequests.series : chartData.series
              }
              type="bar"
              width="100%"
              height={500}
            />
          </DashboardContainer>

          {transfers_requests.length >= 1 && (
            <DashboardContainer className="">
              <div className="p-1 pb-4 flex items-center">
                <section className="flex-1">
                  <h3 className="text-2xl font-bold text-gray-600">
                    Transfer requests
                  </h3>
                </section>
                <section>
                  <ExportToExcel
                    fileData={transfers_requests}
                    fileName={`TRANSFER REQUESTS REPORT ${
                      selected_position === "0"
                        ? "on country level"
                        : ` on ${selectedDistrict} position`
                    }`}
                    btnName="Download EXCEL"
                  />
                </section>
              </div>
              <table className="w-full text-left border-collapse">
                <thead>
                  <tr>
                    <th className="p-2 border">#</th>
                    <th className="p-2 border">District id</th>
                    <th className="p-2 border">DIstrict name</th>
                    <th className="p-2 border">Male</th>
                    <th className="p-2 border">Female</th>
                    <th className="p-2 border">Total</th>
                    <th className="p-2 border">Vacant posts</th>
                  </tr>
                </thead>
                <tbody>
                  {transfers_requests.map((itm, i) => (
                    <tr
                      key={i}
                      className={`${
                        i % 2 ? "" : "bg-gray-100"
                      } hover:bg-blue-300 bg-opacity-20 cursor-pointer`}
                    >
                      <td className={`p-2 border`}>{i + 1}</td>
                      <td className={`p-2 border`}>{itm.district_code}</td>
                      <td className={`p-2 border`}>{itm.district_name}</td>
                      <td
                        className={`p-2 border ${
                          tabGraph === false
                            ? "bg-green-200 bg-opacity-30 font-bold text-green-900"
                            : ""
                        }`}
                      >
                        {itm.total_male}
                      </td>
                      <td
                        className={`p-2 border ${
                          tabGraph === false
                            ? "bg-blue-200 bg-opacity-30 font-bold text-blue-800"
                            : ""
                        }`}
                      >
                        {itm.total_female}
                      </td>
                      <td
                        className={`p-2 border ${
                          tabGraph === true
                            ? "bg-yellow-200 bg-opacity-30 font-bold text-yellow-700"
                            : ""
                        }`}
                      >
                        {itm.total_transfer_request}
                      </td>
                      <td
                        className={`p-2 border ${
                          tabGraph === true
                            ? "bg-blue-200 bg-opacity-30 font-bold text-blue-800"
                            : ""
                        }`}
                      >
                        {itm.district_vacant}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </DashboardContainer>
          )}
        </>
      )}
    </>
  );
};
export default TransferRequestDashboard;
