import React, { FC, ReactElement, useEffect, useState } from "react";
import { FaFemale, FaMale } from "react-icons/fa";
import { MdAccessible, MdGroup, MdOutlineHouse } from "react-icons/md";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Chart from "react-apexcharts";
import Button from "../../components/Buttons/Button";
import {
  FC_GetTransferDistrictById,
  TransferDashboard_Request,
  TransferRequestByDIstrict__Interface,
} from "../../actions";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Alert from "../../components/Alert/Alert";
import CandidatesListByDIstrict from "./CandidatesListByDIstrict";

type SelectedDistrictTransferRequestProps = {
  district: TransferDashboard_Request;
  close: () => void;
};

const SelectedDistrictTransferRequest: FC<
  SelectedDistrictTransferRequestProps
> = ({ close, district }): ReactElement => {
  const [error, setError] = useState<{
    target: "error" | "success" | null;
    msg: string;
  }>({ msg: "", target: null });
  const [loading, setLoading] = useState(true);
  const [candidates_list, setCandidates_list] = useState(false);
  const [dataRes, setData] =
    useState<TransferRequestByDIstrict__Interface | null>(null);
  useEffect(() => {
    if (loading === true) {
      FC_GetTransferDistrictById(
        district.district_code,
        "1",
        (status, data, msg) => {
          if (status) {
            setData(data);
          } else {
            setError({ target: "error", msg: msg });
          }
          setLoading(false);
        }
      );
    }
  }, [district.district_code, loading]);

  let chartData = {
    options: {
      plotOptions: {
        bar: {
          horizontal: true,
        },
      },

      dataLabels: {
        enabled: true,
      },

      chart: {
        id: "basic-bar",
        stacked: true,

        toolbar: {
          show: true,
        },
        zoom: {
          enabled: true,
        },
      },

      xaxis: {
        categories: dataRes
          ? dataRes.transfers_requests.map((itm) => [
              itm.position_code,
              itm.position_name,
            ])
          : [],
      },
    },
    series: [
      {
        name: "Female",
        data: dataRes
          ? dataRes.transfers_requests.map((itm) => itm.female)
          : [],
      },
      {
        name: "Male",
        data: dataRes ? dataRes.transfers_requests.map((itm) => itm.male) : [],
      },
    ],
  };

  if (dataRes == null)
    return (
      <DashboardContainer>
        <h2>No data found</h2>
      </DashboardContainer>
    );

  if (candidates_list === true)
    return (
      <CandidatesListByDIstrict
        district={district}
        close={() => setCandidates_list(false)}
      />
    );

  return (
    <>
      <div className="lg:sticky top-10 z-50">
        <DashboardContainer
          noPadding
          className="flex py-2 mx-2 px-3 flex-col items-center lg:flex-row"
        >
          {error.target === "error" && (
            <Alert theme="danger">{error.msg}</Alert>
          )}
          {error.target === "success" && (
            <Alert theme="success">{error.msg}</Alert>
          )}
          <div className="pr-2">
            <Button theme="danger-light" onClick={close}>
              Back
            </Button>
          </div>
          <div className="flex-1">
            <h1 className="text-2xl font-bold">
              {district.district_name} district{" "}
            </h1>
          </div>
          <div className="">
            <Button
              theme="accent-light"
              onClick={() => setCandidates_list(true)}
            >
              View candidates list
            </Button>
          </div>
        </DashboardContainer>
      </div>

      <div className="animate__animated animate__zoomIn animate__faster">
        <DashboardContainer
          className="px-2 grid-col-2 grid sm:grid-cols-3 xl:grid-cols-6 gap-2 pt-3"
          noPadding
          transparent
        >
          {/* {console.log(dataRes)} */}
          {[
            {
              icon: MdGroup,
              title: "Total candidates",
              value: dataRes.male + dataRes.female,
            },
            { icon: FaFemale, title: "Female", value: dataRes.female },
            { icon: FaMale, title: "Total male", value: dataRes.male },
            {
              icon: MdGroup,
              title: "Total positions",
              value: dataRes.transfers_requests.length,
            },
            {
              icon: MdOutlineHouse,
              title: "Married",
              value: dataRes.married,
              value_2: dataRes.transfers_requests.length,
            },
            {
              icon: MdAccessible,
              title: "With disability",
              value: dataRes.disability,
            },
          ].map((itm, i) => {
            let Icon = itm.icon;
            return (
              <div
                key={i}
                className="flex items-center p-1 rounded border bg-white"
              >
                <section className="m-3 text-4xl bg-yellow-600 bg-opacity-10 rounded-full p-2 text-yellow-800">
                  <Icon />
                </section>
                <section>
                  <h5 className="text-gray-500 m-0">{itm.title}</h5>
                  <h2 className="text-yellow-800 font-bold text-3xl -mt-1">
                    {itm.value}{" "}
                    {itm?.value_2 && (
                      <span className="font-light text-gray-400">
                        of {itm?.value_2}
                      </span>
                    )}
                  </h2>
                </section>
              </div>
            );
          })}
        </DashboardContainer>

        <DashboardContainer className="">
          <Chart
            options={chartData.options}
            series={chartData.series}
            type="bar"
            width="100%"
            height={500}
          />
        </DashboardContainer>
        <DashboardContainer className="">
          <div className="p-1 pb-4 flex items-center">
            <section className="flex-1">
              <h3 className="text-2xl font-bold text-gray-600">
                Transfer requests
              </h3>
            </section>
            <section>
              <ExportToExcel
                fileData={dataRes.transfers_requests}
                fileName={`${district.district_name} District Total Transfer`}
                btnName="Download EXCEL"
              />
            </section>
          </div>
          <table className="w-full text-left border-collapse">
            <thead>
              <tr>
                <th className="p-2 border">#</th>
                <th className="p-2 border">Position code</th>
                <th className="p-2 border">Position name</th>
                <th className="p-2 border">Qualification</th>
                <th className="p-2 border">Male</th>
                <th className="p-2 border">Female</th>
                <th className="p-2 border">Married</th>
                <th className="p-2 border">Single</th>
                <th className="p-2 border">Disability</th>
                <th className="p-2 border">Total</th>
              </tr>
            </thead>
            <tbody>
              {dataRes.transfers_requests.map((itm, i) => (
                <tr
                  key={i}
                  className={`${
                    i % 2 ? "" : "bg-gray-100"
                  } hover:bg-blue-300 bg-opacity-20 cursor-pointer`}
                >
                  <td className={`p-2 border`}>{i + 1}</td>
                  <td className={`p-2 border`}>{itm.position_code}</td>
                  <td className={`p-2 border`}>{itm.position_name}</td>
                  <td className="p-2 border">{itm.qualification_name}</td>
                  <td
                    className={`p-2 border bg-green-200 bg-opacity-30 font-bold text-green-900`}
                  >
                    {itm.male}
                  </td>
                  <td
                    className={`p-2 border bg-blue-200 bg-opacity-30 font-bold text-blue-800`}
                  >
                    {itm.female}
                  </td>

                  <td
                    className={`p-2 border bg-blue-200 bg-opacity-30 font-bold text-blue-800`}
                  >
                    {itm.married}
                  </td>
                  <td
                    className={`p-2 border bg-blue-200 bg-opacity-30 font-bold text-blue-800`}
                  >
                    {itm.single}
                  </td>
                  <td
                    className={`p-2 border bg-yellow-200 bg-opacity-30 font-bold text-yellow-700`}
                  >
                    {itm.disability}
                  </td>
                  <td
                    className={`p-2 border bg-yellow-200 bg-opacity-30 font-bold text-yellow-700`}
                  >
                    {itm.male + itm.female}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </DashboardContainer>
      </div>
    </>
  );
};
export default SelectedDistrictTransferRequest;
