import React, { FC, ReactElement, useState } from "react";
import { HiChip } from "react-icons/hi";
import { RoundData_Interface } from "../../actions";
import Button from "../../components/Buttons/Button";
import Heading from "../../components/Heading/Heading";

type RoundItemTransferProps = {
  round: RoundData_Interface;
};

const RoundItemTransfer: FC<RoundItemTransferProps> = ({
  round,
}): ReactElement => {
  const {
    candidates,
    district_name,
    email,
    full_name,
    phone_numbers,
    round_id,
    schools,
    total_candidate_request_transfer,
    total_schools,
    total_transferred,
  } = round;
  /* function body */
  const [tab, setTab] = useState<"1" | "2">("2");
  return (
    <div className="p-2 bg-white mb-3 rounded shadow ">
      <div className="flex items-center">
        <section className="text-3xl text-blue-400 pr-2">
          <HiChip />
        </section>
        <section className="flex-1">
          <h3 className="font-bold text-lg">{district_name} district</h3>
          <p className="text-sm -mt-1 text-gray-400">
            Round: <b>{round_id}</b>
          </p>
        </section>

        <div className="flex">
          {[
            {
              title: "Total requests",
              value: total_candidate_request_transfer,
              className:
                "text-yellow-800 bg-yellow-800 bg-opacity-5 rounded mx-1 border",
            },
            {
              title: "Total schools",
              value: total_schools,
              className:
                "text-blue-800 bg-blue-800 bg-opacity-5 rounded mx-1 border",
            },
            {
              title: "Transferred",
              value: total_transferred,
              className:
                "text-green-800 bg-green-800 bg-opacity-5 rounded mx-1 border",
            },
          ].map((el, i) => (
            <section key={i} className={`px-2  ${el.className}`}>
              <label className="text-sm ">{el.title}</label>
              <h2 className="text-xl font-bold -mt-1">{el.value}</h2>
            </section>
          ))}
        </div>
      </div>
      <div className="mx-auto text-center">
        <Button
          theme={tab === "2" ? "accent-light" : "default"}
          size="sm"
          onClick={() => setTab("2")}
        >
          Transferred candidates
        </Button>
        <Button
          theme={tab === "1" ? "accent-light" : "default"}
          size="sm"
          onClick={() => setTab("1")}
        >
          Transfers requests
        </Button>
      </div>

      <div className="border-t mt-2">
        {tab === "1" && (
          <table className="border w-full">
            <thead>
              <tr>
                <th className="p-1 border">#</th>
                <th className="p-1 border">Position code</th>
                <th className="p-1 border">School code</th>
                <th className="p-1 border">Vacant</th>
              </tr>
            </thead>
            <tbody>
              {schools.length <= 0 ? (
                <tr>
                  <td className="py-8 text-center"></td>
                </tr>
              ) : (
                schools.map((itm, i) => (
                  <tr
                    key={i}
                    className="hover:bg-yellow-800 hover:bg-opacity-20"
                  >
                    <td className="p-1 border">{i + 1}</td>
                    <td className="p-1 border">{itm.position_id}</td>
                    <td className="p-1 border">{itm.school_code}</td>
                    <td className="p-1 border">{itm.vacant_posts}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        )}

        {tab === "2" && (
          <table className="border w-full">
            <thead>
              <tr>
                <th className="p-1 border">#</th>
                <th className="p-1 border">Candidate</th>
                <th className="p-1 border">Position</th>
                <th className="p-1 border">School</th>
                <th className="p-1 border">DOB</th>
                <th className="p-1 border">Hire date</th>
                <th className="p-1 border">Martial..</th>
                <th className="p-1 border">Gender</th>
                <th className="p-1 border">Disability</th>
                <th className="p-1 border">Schools</th>
                <th className="p-1 border">Date of app</th>
              </tr>
            </thead>
            <tbody>
              {candidates.length <= 0 ? (
                <tr>
                  <td colSpan={20} className="py-8 text-center">
                    <Heading theme="default" className="opacity-50">
                      No candidates found!
                    </Heading>
                  </td>
                </tr>
              ) : (
                candidates.map((itm, i) => (
                  <tr
                    key={i}
                    className="text-sm text-center hover:bg-yellow-800 hover:bg-opacity-10"
                  >
                    <td className="p-0.5 border">{i + 1}</td>
                    <td className="p-0.5 border" title="Candidate">
                      {itm.name}
                    </td>
                    <td className="p-0.5 border" title="Position id">
                      {itm.position_id}
                    </td>
                    <td className="p-0.5 border" title="School code">
                      {itm.school_code}
                    </td>
                    <td className="p-0.5 border text-sm" title="DOB">
                      {itm.dob}
                    </td>
                    <td className="p-0.5 border text-sm" title="Hired date">
                      {itm.hire_date}
                    </td>
                    <td className="p-0.5 border" title="Martial status">
                      {itm.martial_status}
                    </td>
                    <td className="p-0.5 border" title="Gender">
                      {itm.sex}
                    </td>
                    <td className="p-0.5 border text-center" title="Disability">
                      {itm.disability === true ? (
                        <span className="p-1 rounded bg-red-200 text-red-800 font-bold px-2">
                          No
                        </span>
                      ) : (
                        <span className="p-1 rounded bg-red-200 text-red-800 font-bold px-2">
                          Yes
                        </span>
                      )}
                    </td>
                    <td className="p-0.5 border">
                      {itm.schools.map((el) => (
                        <span
                          key={el}
                          className={`inline-block p-1 ${
                            itm.school_code === el
                              ? "text-green-800 bg-green-200"
                              : "bg-gray-200 text-black"
                          }  mx-1 rounded font-bold`}
                          title={
                            itm.school_code === el ? "Approved" : "Rejected"
                          }
                        >
                          {el}
                        </span>
                      ))}
                    </td>

                    <td className="p-0.5 border" title="Date of application">
                      {itm.date_of_application}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        )}
        <div className="py-2 text-sm text-gray-500">
          Done by:{" "}
          <label className="font-bold text-black">
            {full_name} {"("}
            {email} | {phone_numbers}
            {")"}
          </label>
        </div>
      </div>
    </div>
  );
};
export default RoundItemTransfer;
