//
import React, { FC, ReactElement, useMemo, useState } from "react";
import {
  RoundData_Interface,
  RoundData__Candidate_Interface,
  RoundData__Schools_Interface,
} from "../../actions";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Loading from "../../components/Loading/Loading";
import AllRounds from "./AllRounds";
import RoundItemTransfer from "./RoundItemTransfer";

type TransferLogsProps = {
  rounds: RoundData_Interface[];
  loading: boolean;
  getRounds: () => void;
};

const TransferLogs: FC<TransferLogsProps> = ({
  rounds,
  loading,
  getRounds,
}): ReactElement => {
  /* function body */
  const [tab, setTab] = useState<"table-view" | "grouped-view">("table-view");
  let data: {
    candidates: RoundData__Candidate_Interface[];
    schools: RoundData__Schools_Interface[];
  } = useMemo(() => {
    let temp_candidates: RoundData__Candidate_Interface[] = [];
    let temp_schools: RoundData__Schools_Interface[] = [];
    for (const round of rounds) {
      temp_candidates = [...temp_candidates, ...round.candidates];
      temp_schools = [...temp_schools, ...round.schools];
    }
    return {
      candidates: temp_candidates,
      schools: temp_schools,
    };
  }, [rounds]);

  return (
    <>
      <DashboardContainer>
        <div className="flex items-center">
          <div className="flex-1">
            <h2 className="text-2xl font-bold">Transfer logs</h2>
          </div>
          <div className="flex-1  text-right">
            <div className="p-1 bg-gray-200 rounded-lg inline-block">
              <button
                className={`p-1 ${
                  tab === "table-view" ? "bg-yellow-600 text-white" : ""
                } rounded-lg px-3`}
                onClick={() => setTab("table-view")}
              >
                Table view
              </button>
              <button
                className={`p-1 ${
                  tab === "grouped-view" ? "bg-yellow-600 text-white" : ""
                } rounded-lg px-3`}
                onClick={() => setTab("grouped-view")}
              >
                Grouped view
              </button>
            </div>
          </div>
        </div>
      </DashboardContainer>
      <DashboardContainer transparent>
        {loading ? (
          <div className="text-center py-8">
            <Loading />
          </div>
        ) : tab === "grouped-view" ? (
          <div className="animate__animated animate__zoomIn animate__faster">
            {rounds.map((itm, i) => (
              <RoundItemTransfer round={itm} key={i} />
            ))}
          </div>
        ) : (
          <div className="animate__animated animate__zoomIn animate__faster">
            <AllRounds candidates={data.candidates} schools={data.schools} />
          </div>
        )}
      </DashboardContainer>
    </>
  );
};
export default TransferLogs;
