import React, { FC, ReactElement, useState } from "react";
import {
  RoundData__Candidate_Interface,
  RoundData__Schools_Interface,
} from "../../actions";
// import Button from "../../components/Buttons/Button";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Heading from "../../components/Heading/Heading";

type AllRoundsProps = {
  candidates: RoundData__Candidate_Interface[];
  schools: RoundData__Schools_Interface[];
};

const AllRounds: FC<AllRoundsProps> = ({
  candidates,
  schools,
}): ReactElement => {
  const [tab, setTab] = useState<"1" | "2">("2");
  return (
    <div className="p-2 bg-white mb-3  shadow ">
      <div className="flex items-center">
        <div className="flex items-center">
          <div className="flex">
            {tab === "2" && (
              <section
                className={`px-2  text-yellow-800 bg-yellow-800 bg-opacity-5 rounded mx-1 border`}
              >
                <label className="text-sm ">Total placed</label>
                <h2 className="text-xl font-bold -mt-1">{candidates.length}</h2>
              </section>
            )}
            {tab === "1" && (
              <section
                className={`px-2  text-yellow-800 bg-yellow-800 bg-opacity-5 rounded mx-1 border`}
              >
                <label className="text-sm ">Total schools</label>
                <h2 className="text-xl font-bold -mt-1">{schools.length}</h2>
              </section>
            )}
          </div>
        </div>

        <div className="flex-1 text-center">
          <div className="p-1 mx-auto bg-gray-200 rounded-lg inline-block">
            <button
              className={`p-1 ${
                tab === "2" ? "bg-yellow-600 text-white" : ""
              } rounded-lg px-3`}
              onClick={() => setTab("2")}
            >
              Transferred candidates
            </button>
            <button
              className={`p-1 ${
                tab === "1" ? "bg-yellow-600 text-white" : ""
              } rounded-lg px-3`}
              onClick={() => setTab("1")}
            >
              Transfers requests
            </button>
          </div>
        </div>

        <div>
          {tab === "2" && candidates.length >= 1 && (
            <ExportToExcel
              fileData={candidates.map((itm) => ({
                transfer_id: itm.teacher_transfer_id,
                user_id: itm.user_id,
                candidate: itm.name,
                school_code: itm.school_code,
                disability: itm.disability,
                dob: itm.dob,
                sex: itm.sex,
                martial_status: itm.martial_status,
                hire_date: itm.hire_date,
                date_of_application: itm.date_of_application,
                district_code: itm.district_id,
                position_code: itm.position_id,
                requested_schools: itm.schools.join(", "),
                assigned_school_code: itm.school_code,
                transferred: itm.transferred,
              }))}
              fileName={`TRANSFERRED CANDIDATE ALGORITHM`}
              btnName="Download EXCEL (Transferred candidates)"
            />
          )}
          {tab === "1" && schools.length >= 1 && (
            <ExportToExcel
              fileData={schools}
              fileName={`TRANSFER REQUESTS REPORT `}
              btnName="Download EXCEL (Transfer reports)"
            />
          )}
        </div>
      </div>

      <div className="border-t mt-2">
        {tab === "1" && (
          <table className="border w-full">
            <thead>
              <tr>
                <th className="p-1 border">#</th>
                <th className="p-1 border">Position code</th>
                <th className="p-1 border">School code</th>
                <th className="p-1 border">Vacant</th>
              </tr>
            </thead>
            <tbody>
              {schools.length <= 0 ? (
                <tr>
                  <td className="py-8 text-center"></td>
                </tr>
              ) : (
                schools.map((itm, i) => (
                  <tr
                    key={i}
                    className="hover:bg-yellow-800 hover:bg-opacity-20"
                  >
                    <td className="p-1 border">{i + 1}</td>
                    <td className="p-1 border">{itm.position_id}</td>
                    <td className="p-1 border">{itm.school_code}</td>
                    <td className="p-1 border">{itm.vacant_posts}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        )}

        {tab === "2" && (
          <table className="border w-full">
            <thead>
              <tr>
                <th className="p-1 border">#</th>
                <th className="p-1 border">Candidate</th>
                <th className="p-1 border">Position</th>
                <th className="p-1 border">School</th>
                <th className="p-1 border">DOB</th>
                <th className="p-1 border">Hire date</th>
                <th className="p-1 border">Martial..</th>
                <th className="p-1 border">Gender</th>
                <th className="p-1 border">Disability</th>
                <th className="p-1 border">Schools</th>
                <th className="p-1 border">Date of app</th>
              </tr>
            </thead>
            <tbody>
              {candidates.length <= 0 ? (
                <tr>
                  <td colSpan={22} className="py-8 text-center">
                    <Heading theme="default" className="opacity-50">
                      No candidates found!
                    </Heading>
                  </td>
                </tr>
              ) : (
                candidates.map((itm, i) => (
                  <tr
                    key={i}
                    className="text-sm text-center hover:bg-yellow-800 hover:bg-opacity-10"
                  >
                    <td className="p-0.5 border">{i + 1}</td>
                    <td className="p-0.5 border" title="Candidate">
                      {itm.name}
                    </td>
                    <td className="p-0.5 border" title="Position id">
                      {itm.position_id}
                    </td>
                    <td className="p-0.5 border" title="School code">
                      {itm.school_code}
                    </td>
                    <td className="p-0.5 border text-sm" title="DOB">
                      {itm.dob}
                    </td>
                    <td className="p-0.5 border text-sm" title="Hired date">
                      {itm.hire_date}
                    </td>
                    <td className="p-0.5 border" title="Martial status">
                      {itm.martial_status}
                    </td>
                    <td className="p-0.5 border" title="Gender">
                      {itm.sex}
                    </td>
                    <td className="p-0.5 border text-center" title="Disability">
                      {itm.disability === true ? (
                        <span className="p-1 rounded bg-red-200 text-red-800 font-bold px-2">
                          No
                        </span>
                      ) : (
                        <span className="p-1 rounded bg-red-200 text-red-800 font-bold px-2">
                          Yes
                        </span>
                      )}
                    </td>
                    <td className="p-0.5 border">
                      {itm.schools.map((el) => (
                        <span
                          key={el}
                          className={`inline-block p-1 ${
                            itm.school_code === el
                              ? "text-green-800 bg-green-200"
                              : "bg-gray-200 text-black"
                          }  mx-1 rounded font-bold`}
                          title={
                            itm.school_code === el ? "Approved" : "Rejected"
                          }
                        >
                          {el}
                        </span>
                      ))}
                    </td>

                    <td className="p-0.5 border" title="Date of application">
                      {itm.date_of_application}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};
export default AllRounds;
