import React, { FC, ReactElement, useEffect, useState } from "react";
import {
  FC_CandidatesTransferByDistrict,
  TransferDashboard_Request,
  TransferRequestsListInterface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import ExportToExcel from "../../components/GenerateReport/ExportToExcel";
import Loading from "../../components/Loading/Loading";

type CandidatesListByDIstrictProps = {
  close: () => void;
  district: TransferDashboard_Request;
};

const CandidatesListByDIstrict: FC<CandidatesListByDIstrictProps> = ({
  close,
  district,
}): ReactElement => {
  const [loading, setLoading] = useState(true);
  const [dataRes, setData] = useState<TransferRequestsListInterface[]>([]);
  const [error, setError] = useState("");

  useEffect(() => {
    if (loading === true) {
      FC_CandidatesTransferByDistrict(
        district.district_code,
        (status, data, msg) => {
          setLoading(false);
          if (status === true) {
            setData(data);
          } else {
            setError(msg);
          }
        }
      );
    }
  }, [district.district_code, loading]);

  /* function body */
  return (
    <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
      {error !== "" && <Alert theme="danger">{error}</Alert>}
      <div className="p-1 pb-4 flex items-center border-b">
        <section className="flex-1  flex items-center">
          <Button size="md" theme="accent-light" onClick={close}>
            Back
          </Button>
          <h3 className="text-3xl pl-2 font-bold text-gray-600">
            {district.district_name} district's candidates
          </h3>
        </section>
        <section>
          {loading === false && (
            <ExportToExcel
              fileData={dataRes.map((itm) => ({
                names: itm.teacher_full_name,
                phone: itm.teacher_phone_number,
                position_code: itm.position_code,
                position_name: itm.position_name,
                qualification: itm.qualification_name,
                current_school: `${itm.school_from_info.school_code} - ${itm.school_from_info.school_name} (${itm.school_from_info.district_name})`,
                requested_schools: itm.requested_school_info.map(
                  (itm) =>
                    ` ${itm.school_code} - ${itm.school_name} (${itm.district_name}), `
                ),
                comment: itm.teacher_comment,
                requested_date: itm.teacher_requested_transfer_date,
                transfer_status: itm.requested_status,
                approved_school: itm.approved_school_info?.school_code
                  ? `${itm.approved_school_info?.school_code} - ${itm.approved_school_info?.school_name}`
                  : "",
                transfer_date: itm.incoming_decision_date,
              }))}
              fileName={`${district.district_name} Transfer requests candidates`}
              btnName="Download EXCEL"
            />
          )}
        </section>
      </div>

      {loading ? (
        <div className="p-1 text-center py-8">
          <Loading />
        </div>
      ) : (
        <div className="t-2">
          <table>
            <thead>
              <tr>
                <td className="p-2 border-1">#</td>
                <td className="p-2 border">Names</td>
                <td className="p-2 border">Phone</td>
                <td className="p-2 border">Position code</td>
                <td className="p-2 border">Position name</td>
                <td className="p-2 border">Qualification</td>

                <td className="p-2 border">Current school{`(current)`}</td>
                <td className="p-2 border">Requested school{`(s)`}</td>
                <td className="p-2 border">Comment</td>
                <td className="p-2 border">Requested date</td>
                <td className="p-2 border">Status</td>
                <td className="p-2 border">Approved school</td>
                <td className="p-2 border">Transferred date</td>
              </tr>
            </thead>
            <tbody>
              {dataRes.map((itm, i) => (
                <tr key={i}>
                  <td className="p-2 border-1">{i + 1}</td>
                  <td className="p-2 border">{itm.teacher_full_name}</td>
                  <td className="p-2 border">{itm.teacher_phone_number}</td>
                  <td className="p-2 border">{itm.position_code}</td>
                  <td className="p-2 border">{itm.position_name}</td>
                  <td className="p-2 border">{itm.qualification_name}</td>

                  <td className="p-2 border">
                    {itm.school_from_info.school_code} {"("}
                    {itm.school_from_info.district_name}
                    {") "}
                  </td>
                  <td className="p-2 border">
                    {itm.requested_school_info.map(
                      (itm) =>
                        `${itm.school_code} - ${itm.school_name} (${itm.district_name})`
                    )}
                  </td>
                  <td className="p-2 border">{itm.teacher_comment}</td>
                  <td className="p-2 border">
                    {itm.teacher_requested_transfer_date}
                  </td>
                  <td className="p-2 border">{itm.requested_status}</td>
                  <td className="p-2 border">
                    {itm.approved_school_info
                      ? `${itm.approved_school_info?.school_code} - ${itm.approved_school_info?.school_name}`
                      : ""}
                  </td>
                  <td className="p-2 border">{itm.incoming_decision_date}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </DashboardContainer>
  );
};
export default CandidatesListByDIstrict;
