import React from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetRoundDataByAcademic,
  FC_GetRoundDataByRoundId,
  FC_PublishTransfer,
  FC_RunPlacementAlgorithm,
  RoundData_Interface,
} from "../../actions";
import { StoreState } from "../../reducers";
import TransferAnimation from "./TransferAnimation";
import TransferRequestDashboard from "./TransferRequestDashboard";
import { v4 as UUID } from "uuid";
import RoundItemTransfer from "./RoundItemTransfer";
import TransferLogs from "./TransferLogs";
import Alert from "../../components/Alert/Alert";
// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

enum Tabs {
  ALGORITHM = "ALGORITHM",
  REQUEST_DASHBOARD = "REQUEST-DASHBOARD",
  TRANSFER_LOGS = "TRANSFER-LOGS",
}

interface AppState {
  loading: boolean;
  tab: Tabs;
  loading_algorithm: boolean;
  loading_publish: boolean;
  generated_round_id: string | null;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  rounds: RoundData_Interface[];
  all_rounds: RoundData_Interface[];
  loading_logs: boolean;
}

class _TransferREB extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loading_publish: false,
      tab: Tabs.ALGORITHM,
      loading_algorithm: false,
      generated_round_id: null,
      error: {
        target: null,
        msg: "",
      },
      rounds: [],
      all_rounds: [],
      loading_logs: false,
    };
  }

  // componentDidMount() {

  // }
  getRounds() {
    if (this.props.basic.academic_calender) {
      this.setState({
        loading_logs: true,
      });
      FC_GetRoundDataByAcademic(
        this.props.basic.academic_calender?.academic_year_id,
        (status, data, msg) => {
          // console.log({ FC_GetRoundDataByAcademic: data });

          if (status) {
            this.setState({
              all_rounds: data,
              loading_logs: false,
            });
          } else {
            this.setState({
              loading_logs: false,
              error: {
                target: "main-error",
                msg: msg,
              },
            });
          }
        }
      );
    } else {
      this.setState({
        error: {
          msg: "Academic year does not found!",
          target: "main-error",
        },
      });
    }
  }

  startAlgorithm = () => {
    if (!window.confirm("Are you sure you want to run the algorithm?")) return;

    const user_id = this.props.auth.user?.user_id;
    const academic_id = this.props.basic.academic_calender?.academic_year_id;
    const round_id = UUID();
    if (!user_id)
      return this.setState({
        error: {
          target: "main-error",
          msg: "Your credentials are not valid, please refresh your browser",
        },
      });

    if (!academic_id)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No academic calendar found, please refresh your browser",
        },
      });

    this.setState({
      loading_algorithm: true,
      generated_round_id: round_id,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_RunPlacementAlgorithm(
      { academic_id: academic_id, round_id: round_id, user_id: user_id },
      (status, msg) => {
        // no need to check for error because there are no rounds
        this.RecursiveResultsCheck(round_id);
      }
    );
  };

  publishTransfer = () => {
    if (
      !window.confirm("Are you sure you want to publish the algorithm results?")
    )
      return;

    const user_id = this.props.auth.user?.user_id;
    const academic_id = this.props.basic.academic_calender?.academic_year_id;
    const round_id = UUID();

    if (!user_id)
      return this.setState({
        error: {
          target: "main-error",
          msg: "Your credentials are not valid, please refresh your browser",
        },
      });

    if (!academic_id)
      return this.setState({
        error: {
          target: "main-error",
          msg: "No academic calendar found, please refresh your browser",
        },
      });

    this.setState({
      loading_publish: true,
      generated_round_id: round_id,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_PublishTransfer(
      { academic_id: academic_id, round_id: round_id, user_id: user_id },
      (status, msg) => {
        this.setState({
          error:
            status === false
              ? { msg: msg, target: "main-error" }
              : {
                  target: "main-success",
                  msg: "Placement has been published successfully.",
                },
          loading_publish: false,
        });
      }
    );
  };

  // recursive round
  RecursiveResultsCheck(round_id: string) {
    FC_GetRoundDataByRoundId(round_id, (status, data, msg) => {
      if (status === true) {
        this.setState({
          rounds: data,
          loading_algorithm: false,
          error: {
            target: "main-success",
            msg: "The process executed successfully, check the results below",
          },
        });
      } else {
        setTimeout(() => {
          this.RecursiveResultsCheck(round_id);
        }, 7000);
      }
    });
  }

  selectTab = (el: Tabs) => {
    this.setState({
      tab: el,
    });

    // load the rounds once we get them
    if (el === Tabs.TRANSFER_LOGS) this.getRounds();
  };

  render() {
    return (
      <div
        className={`mx-auto ${
          this.state.tab === Tabs.ALGORITHM ? "max-w-screen-lg" : ""
        }`}
      >
        <div className="container mx-auto">
          {this.state.error.target === "main-error" && (
            <Alert
              theme="danger"
              onClick={() =>
                this.setState({
                  error: {
                    target: null,
                    msg: "",
                  },
                })
              }
            >
              {this.state.error.msg}
            </Alert>
          )}
          {this.state.error.target === "main-success" && (
            <Alert
              theme="success"
              onClick={() =>
                this.setState({
                  error: {
                    target: null,
                    msg: "",
                  },
                })
              }
            >
              {this.state.error.msg}
            </Alert>
          )}
        </div>

        <div className="p-2">
          <section className=" max-w-screen-sm mx-auto grid grid-cols-3 text-center p-1 rounded-lg border  bg-white  w-auto">
            {[
              {
                key: Tabs.REQUEST_DASHBOARD,
                title: "Transfer requests",
              },
              {
                key: Tabs.ALGORITHM,
                title: "Algorithm",
              },
              {
                key: Tabs.TRANSFER_LOGS,
                title: "Transfer logs",
              },
            ].map((itm) => (
              <button
                onClick={this.selectTab.bind(this, itm.key)}
                key={itm.key}
                className={`p-1.5 rounded-lg font-bold ${
                  this.state.tab === itm.key ? "text-white  bg-yellow-600" : ""
                }`}
              >
                {itm.title}
              </button>
            ))}
          </section>
        </div>

        {this.state.tab === Tabs.ALGORITHM && (
          <>
            <TransferAnimation
              loading={this.state.loading_algorithm}
              startAlgorithm={this.startAlgorithm}
              loading_publish={this.state.loading_publish}
              publishTransfer={this.publishTransfer}
            />

            {this.state.rounds.length >= 1 && (
              <div className="rounded border mx-3 mt-4">
                <div className="border-b flex items-center p-3 ">
                  <h2 className="text-xl">Results</h2>
                </div>
                {this.state.rounds.map((itm, i) => (
                  <RoundItemTransfer round={itm} key={i} />
                ))}
              </div>
            )}
          </>
        )}

        {this.state.tab === Tabs.REQUEST_DASHBOARD && (
          <>
            <TransferRequestDashboard />
          </>
        )}

        {this.state.tab === Tabs.TRANSFER_LOGS && (
          <>
            <TransferLogs
              rounds={this.state.all_rounds}
              loading={this.state.loading_logs}
              getRounds={this.getRounds}
            />
          </>
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const TransferREB = connect(mapStateToProps, {})(_TransferREB);
